/* harmony default export */ __webpack_exports__["default"] = ("<!-- <p class=\"title3 text-center\" id=\"section-7\">DATA Management </p>\n\n<div class=\"row\">\n  <div class=\"col-md-6\">\n      <img src=\"assets/datam.jpg\" class=\"img-fluid imgmar\" alt=\"Responsive image\" height=\"216px\">\n  </div>\n  <div class=\"col-md-6\">\n    <p class=\"homep\">\n      Data Management is a subset of Information Management. It comprises all disciplines\n      related to managing data as a valuable, organizational resource. Specifically, it’s the process\n      of creating, obtaining, transforming, sharing, protecting, documenting and preserving data.\n    </p>\n  </div>\n</div>\n\n\n<p class=\"title3 text-center\" id=\"section-8\">VISUAL ANALYTICS </p>\n\n<div class=\"row\">\n  <div class=\"col-md-6\">\n      <img src=\"assets/visual.jpg\" class=\"img-fluid imgmar\" alt=\"Responsive image\" height=\"216px\">\n  </div>\n  <div class=\"col-md-6\">\n    <p class=\"homep\">\n      Visual Analytics is the science of analytical reasoning supported by\ninteractive visual interfaces. Today, data is produced at an incredible rate and the\nability to collect and store the data is increasing at a faster rate than the ability to\nanalyze it. Visual Analytics can be seen as an integral approach combining\nvisualization, human factors, and data analysis. The figure illustrates the research\nareas related to Visual Analytics. Besides visualization and data analysis, especially\nhuman factors, including the areas of cognition and perception, play an important role\nin the communication between the human and the computer, as well as in the\ndecision-making process.\n    </p>\n    \n  </div>\n</div>\n<p class=\"title3 text-center\" id=\"section-9\">DATA BLENDING </p>\n\n<div class=\"row\">\n  <div class=\"col-md-6\">\n      <img src=\"assets/datab.jpg\" class=\"img-fluid imgmar\" alt=\"Responsive image\" height=\"216px\">\n  </div>\n  <div class=\"col-md-6\">\n    <p class=\"homep\">\n      Data blending is a method for combining data from multiple sources. Data\nblending brings in additional information from a secondary data source and displays it\nwith data from the primary data source directly in the view.\n    </p>\n    <ul class=\"homep\">\n      \n      <li>Data Acquisition: Identify and obtain access to the data within your sources</li>\n      <li> Joining Data: Combine the edited data for further use and analysis</li>\n      <li>Data Cleansing: Redesign the data into a usable/functional format and\n        correct/remove any bad data</li>\n    </ul>\n  </div>\n</div>\n<p class=\"title3 text-center\" id=\"section-10\">Governance &amp; Compliance </p>\n\n<div class=\"row\">\n  <div class=\"col-md-6\">\n      <img src=\"https://www.sscsrl.com/wp-content/uploads/2018/09/shutterstock_585075550.jpg\" class=\"img-fluid imgmar\" alt=\"Responsive image\" height=\"216px\">\n  </div>\n  <div class=\"col-md-6\">\n    <ul class=\"homep\">\n      <li> Forensic Tools &amp; Services - CySpace Global™ has experienced\n        consultant and highly trained and skilled professional who works to\n        provide consulting services related to forensic science. Our work involves examining evidence, evaluating cases and reconstructing\n        crime scenes. We deal with different tools.\n        Tools: SANS SIFT workstation, Volatility, FTK, EnCase, FireEye\n        Red Line, Autopsy, X-Ways Forensics, Exif- Tools.</li>\n      <li> Fraud Detection and Prevention - CySpace Global™ provides Fraud\n        detection and prevention services for their clients to review\n        customer transactions and search for patterns of behavior that\n        typically suggest fraudulent activity. These include things like the\n        location, size, and frequency of the transaction, as well as more\n        sophisticated metrics based on customer history profiles.\n        Tools- Geolocation, Proxy Piercing, Fraud Scoring, Device\n        Fingerprinting, Address Verification, Rescreening Orders, F-Case,\n        Bolt, RSA Adaptive Authentication </li>\n      <li> Governance Regulatory &amp; Compliance - CySpace Global™ GRC\n        Services  help clients tackle the broad issues of corporate\n        governance, enterprise risk management with reputation and ensure\n        compliance, and deliver real business values, while complying with\n        regulatory requirements We can help organizations identify,\n        remediate, monitor, exploit and manage enterprise risks in addition\n        to coordinating the utilization of people, process and technology to\n        improve GRC effectiveness and help manage costs.\n        Tools- Druva inSync, RSA Archer, PowerDMS, MetricStream, IBM\n        OpenPages</li>\n      \n\n    </ul>\n  </div>\n</div> -->");

