/* harmony default export */ __webpack_exports__["default"] = ("<!-- \n<p class=\"title3 text-center\" id=\"section-16\">DATA OPTIMISATION</p>\n\n<div class=\"row\">\n  <div class=\"col-md-6\">\n      <img src=\"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQ5qdkvw92Sg8SthOuTMqIGXJQYWDNNftzjvS2CZs5NkrU1zk1x&s\" class=\"img-fluid imgmar\" alt=\"Responsive image\" height=\"216px\">\n\n  </div>\n  <div class=\"col-md-6\">\n    <p class=\"homep\">\n        Databases can store tremendous amounts of information, petabytes of information. Specific\n        bits of data are accessed by queries written in a particular interface language, such as SQL.\n        Database optimization involves maximizing the speed and efficiency with which data is\n        retrieved. Database designers, administrators, and analysts work together to optimize\n        system performance through diverse methods. Thoughtful design that carefully addresses\n        functional needs is the foundation of performance enhancement.\n    </p>\n  </div>\n</div>\n\n\n\n\n\n<p class=\"title3 text-center\" id=\"section-17\">DATABASE VIRTUALISATION</p>\n\n<div class=\"row\">\n  <div class=\"col-md-6\">\n      <img src=\"https://www.astera.com/wp-content/uploads/2019/06/Asset-1@2x.png \"  alt=\"Responsive image\" height=\"216px\">\n\n  </div>\n  <div class=\"col-md-6\">\n    <p class=\"homep\">\n        Data virtualization integrates data from disparate sources without copying or\nmoving the data, thus giving users a single virtual layer that spans multiple\napplications, formats, and physical locations. This means faster, easier access to\ndata.\n    </p>\n    <ul class=\"homep\">\n        <li><b>Zero Replication:</b> Data is not moved or copied. Instead, it stays where it is\n            and is connected to other sources, no matter the location. This greatly\n            improves the speed at which users can access data.</li>\n        <li><b>Abstraction:</b> Business users can access the data without concern about\n            where it resides.</li>\n        <li><b>Real-Time Access:</b> As source data is updated or changed, the data is\n            available immediately.</li>\n        <li><b>Agility:</b> Changes can be made without impacting the business. Data\n            Virtualization facilitates a universal semantic layer across multiple consuming\n            applications.</li>    \n        </ul>\n  </div>\n</div>\n\n\n\n<p class=\"title3 text-center\" id=\"section-18\">DATABASE MONITORING</p>\n\n<div class=\"row\">\n  <div class=\"col-md-6\">\n      <img src=\"https://hlassets.paessler.com/common/files/illustration/database-monitoring.png\" class=\"img-fluid imgmar\" alt=\"Responsive image\" height=\"216px\">\n\n  </div>\n  <div class=\"col-md-6\">\n    <p class=\"homep\">\n        Databases are at the heart of many enterprises&#39; most critical business\n        processes. Application complexity is on the rise and IT infrastructures are becoming\n        far more diverse. With complex applications working across heterogeneous\n        infrastructures, database monitoring that provides fast, accurate problem resolution is\n        critical to helping IT troubleshoot problems before they affect end-users.\n    </p>\n    <ul class=\"homep\">\n        <li>Reduce the time and resources needed for supporting databases and the\n            underlying IT infrastructure</li>\n        <li>Improve end-user performance</li>\n        <li>Improve capacity planning by determining whether issues can be resolved by\n            upsizing database or server configurations</li>\n        <li>Proactively troubleshoot performance problems before they reach end-users</li>    \n        </ul>\n  </div>\n</div>\n\n<p class=\"title3 text-center\" id=\"section-19\">DEVELOPMENT AND DBA TOOLS</p>\n\n<div class=\"row\">\n  <div class=\"col-md-6\">\n      <img src=\"https://blueturtle.co.za/wp-content/uploads/2014/10/2nd-development-and-dba-tools-845x321.jpg\" class=\"img-fluid imgmar\" alt=\"Responsive image\" height=\"216px\">\n\n  </div>\n  <div class=\"col-md-6\">\n    <p class=\"homep\">\n        Develop and deploy high quality, high performing tools, and methods nearly\ntwice as fast as comparative toolsets – without cutting corners. Get the power to\nimplement consistent, repeatable database development processes so you can meet\nbusiness requirements and make your organization more agile. Extensive automation\nand collaboration functionality make it easy to blaze through development cycles and\nminimize risks.\n    </p>\n  </div>\n</div>\n\n<p class=\"title3 text-center\" id=\"section-20\">SECURITY AND AUDIT</p>\n\n<div class=\"row\">\n  <div class=\"col-md-6\">\n      <img src=\"https://d3njjcbhbojbot.cloudfront.net/api/utilities/v1/imageproxy/https://coursera-course-photos.s3.amazonaws.com/84/33b570acdd11e69bf1c9342aa1a7c7/Security_block.jpg?auto=format%2Ccompress&dpr=2.625\" class=\"img-fluid imgmar\" alt=\"Responsive image\" height=\"216px\">\n\n  </div>\n  <div class=\"col-md-6\">\n    <p class=\"homep\">\n        An information security audit occurs when a technology team conducts an\n        organizational review to ensure that the correct and most up-to-date processes and\n        infrastructure are being applied.\n    </p>\n    <p class=\"homep\">\n        An audit also includes a series of tests that guarantee that information\n        security meets all expectations and requirements within an organization. During this       \n        process, employees are interviewed regarding security roles and other relevant\n        details.\n    </p>\n  </div>\n</div> -->");

