/* harmony default export */ __webpack_exports__["default"] = ("<!-- <p class=\"title3 text-center\" id=\"section-11\"> ENTERPRISE DATA MANAGEMENT</p>\n\n<div class=\"row\">\n  <div class=\"col-md-6\">\n      <img src=\"https://amarketresearchgazette.com/wp-content/uploads/2019/04/Global-Enterprise-Data-Management-Software-Market.jpg\" class=\"img-fluid imgmar\" alt=\"Responsive image\" height=\"216px\">\n  </div>\n  <div class=\"col-md-6\">\n    <p class=\"homep\">\n        Enterprise data management (EDM) is an organization&#39;s ability to effectively create,\n        integrate, disseminate and manage data for all enterprise applications, processes and\n        entities requiring timely and accurate data delivery.\n    </p>\n  </div>\n</div>\n<p class=\"title3 text-center\" id=\"section-12\"> DATA GOVERNANCE</p>\n\n<div class=\"row\">\n  <div class=\"col-md-6\">\n      <img src=\"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRPRwTGO6eX5S8J7yZpuNx-CTy_rD55yVvZQVuO-DXM_QTUMcD3&s\" class=\"img-fluid imgmar\" alt=\"Responsive image\" height=\"216px\">\n  </div>\n  <div class=\"col-md-6\">\n    <p class=\"homep\">\n        Most organizations should have some form of data governance to prevent\n        sensitive information from getting into the wrong hands. However, large companies\n        and regulated industries, such as healthcare or banking, have the most at stake. The\n        amount of effort and expense spent on data governance should be and usually is,\n        commensurate with the amount of risk.\n    </p>\n    <p class=\"homep\">\n        Data governance requires an organization to understand and take stock of\nregulatory requirements, legal requirements, and business best practices which their\nInternal data must meet, establish rules, and adopt automated and human processes to see\nthat the rules are enforced.\n    </p>\n  </div>\n</div>\n<p class=\"title3 text-center\" id=\"section-13\"> DATA MIGRATION</p>\n\n<div class=\"row\">\n  <div class=\"col-md-6\">\n      <img src=\"https://www.everteam.com/wp-content/uploads/2017/12/shutterstock_197929265-1030x821.jpg\" class=\"img-fluid imgmar\" alt=\"Responsive image\" height=\"216px\">\n  </div>\n  <div class=\"col-md-6\">\n    <p class=\"homep\">\n        Data migration is the process of moving data from one location to another,\none format to another, or one application to another. Generally, this is the result of\nintroducing a new system or location for the data. The business driver is usually an\napplication migration or consolidation in which legacy systems are replaced or\naugmented by new applications that will share the same dataset.\n    </p>\n    <ul class=\"homep\">\n        <li> Cloud Migration: It is the process of moving data, applications and all-\n            important business elements from on-premise data center to the cloud, or\n            from one cloud to another.</li>\n        <li> Application Migration: Involves transfer of application programs to a modern\n            environment. It may move an entire application system from on-premise IT\n            Center to the cloud or between clouds.</li>\n        <li> Storage Migration: It is the process of moving data to a modern system from\n            outdated arrays. It enhances performance while offering cost-effective\n            scaling.</li>\n    </ul>\n    \n  </div>\n</div>\n<p class=\"title3 text-center\" id=\"section-14\"> DATA QUALITY</p>\n\n<div class=\"row\">\n  <div class=\"col-md-6\">\n      <img src=\"http://www.marketreportgazette.com/wp-content/uploads/2019/10/Data-Quality-Software-Market.png\" class=\"img-fluid imgmar\" alt=\"Responsive image\" height=\"216px\">\n  </div>\n  <div class=\"col-md-6\">\n    <p class=\"homep\">\n        Data quality refers to the overall utility of a dataset(s) as a function of its\nability to be easily processed and analyzed for other uses, usually by a database,\ndata warehouse, or data analytics system. To be of high quality, data must be\nconsistent and unambiguous.\n    </p>\n    <p class=\"homep\">\n        Data quality issues are often the result of database merges or systems/cloud\nintegration processes in which data fields that should be compatible are not due to\nschema or format inconsistencies. Data that is not high quality can undergo data\ncleansing to raise its quality.\n    </p>\n    \n  </div>\n</div>\n<p class=\"title3 text-center\" id=\"section-15\"> INTEGRATION</p>\n\n<div class=\"row\">\n  <div class=\"col-md-6\">\n      <img src=\"http://amconsultingke.com/wp-content/uploads/2019/01/Hardware-Software-Integration.png\" class=\"img-fluid imgmar\" alt=\"Responsive image\" height=\"216px\">\n  </div>\n  <div class=\"col-md-6\">\n    <p class=\"homep\">\n        Data integration is a process in which heterogeneous data is retrieved and\ncombined as an incorporated form and structure. Data integration allows different data\ntypes (such as data sets, documents, and tables) to be merged by users, organizations,\nand applications, for use as personal or business processes and/or functions.\n    </p>\n    <p class=\"homep\">\n        Data integration primarily supports the analytical processing of large data sets by\n        aligning, combining and presenting each data set from organizational departments and\n        external remote sources to fulfill integrator objectives.\n    </p>\n    \n  </div>\n</div>\n\n\n\n -->\n");

